@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  background-color: #dfdfdf;
  background-color: var(--bodyColor);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

:root {
  --headerColor: #131921;
  --smallNavbarColor: #232f3e;
  --searchButtonBg: #faaa42;
  --sectionBGColor: #eaeded;
  --linkColor: #288798;
  --headerFontColor: #0f1111;
  --footerColorFirst: #37475a;
  --footerColorSecond: #232f3e;
  --footerColorThree: #131a22;
  ---mainColor: #fff;
  --darkColor: #000;
  --dropDownColor: #f3f3f3;
  --smoothWhite: #666;
  --lightSmook: rgb(202, 202, 202);
  --bodyColor: #dfdfdf;
  --loveRed: rgb(223, 111, 47);
  ---smoothGray: rgb(83, 83, 83);
  --red: rgb(160, 15, 15);
}

a {
  text-decoration: none;
}

.TopNavbar p {
  font-size: 0.8rem;
}

.TopNavbar p a {
  color: var(--lightSmook);
}

.TopNavbar h3 {
  font-size: 1rem;
  color: var(---mainColor);
}

.NavbarDivSecond .SearchDiv {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: auto;
}

.CardSearchDiv {
  width: 1020px;
  height: 300px;
  position: absolute;
  background-color: var(---mainColor);
  z-index: 100;
  left: 0;
  top: 60px;
  padding: 0.6rem 0.8rem;
  overflow: scroll;
  overflow-x: hidden;
  display: none;
}

.ShowDiv {
  display: block;
}

.SearchCard {
  width: 100%;
  height: auto;
  display: flex;
}

.SearchContentDiv h1 {
  color: var(--darkColor);
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.NavbarDivSecond {
  border-radius: 5px;
  /* overflow: hidden; */
}

.NavbarDivSecond .SearchDiv input {
  width: 900px;
  height: 50px;
  outline: none;
  padding: 1rem;
}

.DropDownMenuDiv {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 50px;
  background-color: var(--dropDownColor);
  color: var(--smoothWhite);
}

.DropDownMenuDiv p {
  margin-right: 0.5rem;
}

.DropDownMenuDiv i {
  cursor: pointer;
}

.NavbarDivSecond .SearchDiv .searchIcon {
  width: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  background-color: var(--searchButtonBg);
  color: var(--darkColor);
  border: none;
  cursor: pointer;
}

.WishListCard {
  width: 100px;
  height: 100vh;
  background-color: #fff;
  position: absolute;
  top: 50px;
  left: -12px;
  z-index: 999;
  border-radius: 10px;
  box-shadow: 5px 5px 5px 0px rgba(174, 174, 174, 0.46);
  -webkit-box-shadow: 5px 5px 5px 0px rgba(174, 174, 174, 0.46);
  -moz-box-shadow: 5px 5px 5px 0px rgba(174, 174, 174, 0.46);
  padding: 1rem;
  /* overflow: scroll; */
  /* overflow-x: hidden; */
  transition: all 0.3s ease;
  opacity: 1;
}

.hideCard {
  opacity: 0;
}

.WishListContent {
  text-align: center;
  display: grid;
  place-content: center;
}

.WishListContent h3 {
  color: var(--darkColor);
  font-size: 0.8rem;
}

.WishListContent p {
  color: var(--footerColorFirst) !important;
  margin: 0.3rem 0;
}

.ProductCoverDiv {
  position: relative;
}

.ProductsImgDiv {
  width: 80px;
  height: 100px;
  background-position: center;
  background-size: cover;
  margin: 0.6rem 0;
  overflow: hidden;
  display: flex;
  justify-content: center;
  position: relative;
}

.ProductCoverDiv {
  display: flex;
}

.Shop:hover {
  text-decoration: underline;
}

.ProductsImgDiv img {
  width: auto;
  height: 100%;
  object-fit: contain;
}

.quntityDiv {
  width: 25px;
  height: 25px;
  background-color: var(--darkColor);
  position: absolute;
  right: 10px;
  top: 10px;
  border-radius: 50%;
}

.quntityDiv p {
  color: var(--mainColor) !important;
  font-weight: 600;
}

.WishListCard::-webkit-scrollbar {
  width: 10px;
}

/* Track */
.WishListCard::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
.WishListCard::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

/* Handle on hover */
.WishListCard::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.removeItemButton {
  font-size: 0.6rem;
  padding: 0.2rem 1rem;
  background-color: var(--headerFontColor);
  border: none;
  color: var(---mainColor);
  cursor: pointer;
}

