.TopNavbar {
  width: 100%;
  height: auto;
  padding: 10px 0;
  background-color: var(--headerColor);
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: var(---mainColor);
}

.NavbarDivFirst,
.NavbarDivThree {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.NavbarDivFirst > div,
.NavbarDivThree > div {
  margin: 0 1rem;
}

.NavbarDivFirst p {
  color: var(--lightSmook);
}

.NavbarDivThree p {
  color: var(--lightSmook);
}

.NavbarDivThree a {
  color: var(---mainColor);
}

.ShopDivComponent {
  position: relative;
}

.ShopDivComponent i {
  cursor: pointer;
  margin-right: 0.5rem;
}

.SecondNavbar {
  width: 100%;
  height: auto;
  background-color: var(--smallNavbarColor);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
}

.SecondNavbar p {
  color: var(---mainColor);
  font-weight: 600;
  margin: 0 0.4rem;
  font-size: 0.8rem;
}

.SecondNavbar a {
  color: var(---mainColor);
}

.ProductsCardDiv {
  width: 350px;
  background-color: var(---mainColor);
  padding: 1.5rem;
  margin: 1rem 0.5rem;
  height: auto;
  box-shadow: 5px 5px 5px 0px rgba(174, 174, 174, 0.46);
  -webkit-box-shadow: 5px 5px 5px 0px rgba(174, 174, 174, 0.46);
  -moz-box-shadow: 5px 5px 5px 0px rgba(174, 174, 174, 0.46);
}

.ProductsCardDiv h1 {
  font-size: 1rem;
  margin-bottom: 0.8rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.ProducDiv {
  width: 100%;
  height: 250px;
  display: flex;
  justify-content: center;
}

.ProducDiv img {
  width: auto;
  height: 100%;
  object-fit: contain;
}

.ProductsCardDiv p {
  margin-top: 1rem;
  font-size: 0.8rem;
  cursor: pointer;
}

.PriceDiv {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.PriceDiv > p:nth-child(2):hover {
  color: var(--linkColor);
  text-decoration: underline;
}

.wishListLink {
  background: var(--darkColor);
  padding: 0.3rem 1rem;
  color: var(---mainColor);
}

.BannerSection {
  width: 100%;
  height: auto;
  position: relative;
}

.Container {
  max-width: 1500px;
  margin: auto;
}

.BannerSection .Container {
  position: relative;
  display: grid;
  place-content: center;
}

.BannerSection .Container::before {
  content: "";
  width: 100%;
  height: 70vh;
  background-image: url(https://m.media-amazon.com/images/I/71yr9snSjYL._SX3000_.jpg);
  background-position: center;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.ProductsDiv {
  width: 100%;
  height: auto;
}

.Spacer {
  width: 100%;
  height: 25vh;
}

.ProductsContainer {
  width: 100%;
  height: auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.CustomButtonComponent {
  border: none;
  background-color: var(--footerColorThree);
  color: var(---mainColor);
  padding: 0.8rem 2.3rem;
  margin: 1rem 0;
  margin-right: 1rem;
  cursor: pointer;
}

.AddToCard {
  background-color: var(--loveRed);
}

.ProductsShowDiv {
  width: 100%;
  height: auto;
  padding: 20px 0;
}

.ProductsContainerDiv {
  width: 100%;
  height: 100%;
  display: flex;
}

.ShortImages {
  width: auto;
}

.ShortImages img {
  width: 90px;
}

.ImgDivContent img {
  width: auto;
  height: 600px;
}

.productsContent {
  padding: 0 2rem;
}

.productsContent h2 {
  font-weight: 400;
  font-size: 1.5rem;
}

.RatinDiv {
  display: flex;
  margin: 0.5rem 0;
  align-items: center;
}

.RatinDiv p {
  color: var(--linkColor);
  margin-right: 1rem;
}

.RatinDiv p:hover {
  text-decoration: underline;
}

.AmazoneChoice {
  width: auto;
  padding: 0.5rem;
  background-color: var(--footerColorThree);
  margin-right: 1rem;
  color: var(---mainColor);
  font-size: 0.8rem;
}

.AmazoneChoice span {
  color: var(--loveRed);
}

.MrpDiv {
  padding: 2rem;
  border-top: 1px solid var(--lightSmook);
  border-bottom: 1px solid var(--lightSmook);
}

.MrpDiv p {
  margin: 0.3rem 0;
  color: var(---smoothGray);
}

.MrpDiv span {
  font-size: 1.2rem;
}

.DayOffer {
  color: var(--loveRed);
}

.DropingDiv {
  display: flex;
  align-items: center;
  padding: 1rem 0;
  text-align: center;
}

.DropingDiv > div {
  margin-right: 0.8rem;
}

.DropingDiv img {
  width: 40px;
}

.DropingDiv p {
  font-size: 0.8rem;
  max-width: 120px;
  color: var(--linkColor);
}

.AboutDiv p {
  font-size: 0.9rem;
}

.SignInDiv {
  width: 500px;
  height: auto;
  padding: 2rem;
  box-shadow: 1px 2px 5px -2px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 1px 2px 5px -2px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 1px 2px 5px -2px rgba(0, 0, 0, 0.75);
}

.SignInDiv p {
  margin-top: 1rem;
}
.SignInDiv input {
  width: 100%;
  height: 40px;
  padding: 1rem;
  outline: none;
}

.ButtonGroup {
  display: flex;
}

.WideButton {
  width: 100%;
}

.GoogleButton {
  background-color: #4081ec;
}

.ErrorPara {
  color: var(--red);
}

.SignInDivCover {
  height: 85vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ShopCard {
  width: 100%;
  height: auto;
  padding: 60px 0;
  display: flex;
  justify-content: center;
  align-content: center;
}

.Card {
  width: 100%;
  height: auto;
  padding: 1.3rem;
  background-color: var(---mainColor);
  border-radius: 10px;
}

.Card h1 {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 1rem;
}

.ShopContent {
  width: 100%;
  display: flex;
  align-content: center;
  justify-content: space-around;
  margin: 2rem 0;
  padding: 1rem 0;
  border-top: 1px solid rgb(155, 153, 153);
}

.ShopContent .ShopImgDiv {
  width: auto;
  height: 220px;
  display: flex;
  justify-content: center;
  align-content: center;
  object-fit: contain;
}

.ShopImgDiv img {
  width: auto;
  height: 100%;
}

.SHopContentDiv {
  margin: 0 2rem;
}

.SHopContentDiv h1 {
  font-size: 1.3rem;
}

.SHopContentDiv .inStock {
  color: var(--loveRed);
}

.SHopContentDiv p {
  margin: 0.4rem 0;
  font-size: 0.8rem;
}

.QytDiv {
  display: flex;
  align-content: center;
  margin: 0.5rem 0;
}

.QytDiv p {
  margin-right: 1rem;
  color: var(--linkColor);
  cursor: pointer;
}

.QytDiv p:hover {
  text-decoration: underline;
}

.SubTotalDiv {
  width: 100%;
  height: auto;
  padding: 10px 0;
  border-top: 1px solid var(--lightSmook);
}

.SubTotalDiv h2 {
  float: right;
  font-weight: 600;
  font-size: 1rem;
}

.SubTotalDiv button {
  float: right;
}

.GiftLine span {
  color: var(--linkColor);
}

.SHopContentDiv h5 > span {
  font-weight: 400;
}

.payDiv {
  margin-top: 1rem;
}

.ThankuDiv {
  width: 100%;
  height: auto;
  text-align: center;
  background-color: var(---mainColor);
  padding: 20px;
  margin-top: 2rem;
  border-radius: 10px;
}

.ThankuDiv h1 {
  margin: 1rem 0;
}

.ThankuDiv span {
  color: var(--linkColor);
  cursor: pointer;
}

.ThankuDiv span:hover {
  text-decoration: underline;
}

.youOrderDiv {
  width: 100%;
  height: auto;
  padding: 1rem;
}

.yourOrderCard {
  width: 100%;
  height: auto;
  padding: 2rem;
  margin-top: 2rem;
  background-color: var(---mainColor);
  border-radius: 10px;
}

.yourOrderCard h1 {
  margin-bottom: 1rem;
}

.InnrDivPro {
  width: 100%;
  height: auto;
  padding: 2rem;
  border-top: 1px solid var(--smoothWhite);
}

.YourOrderInnerDiv {
  display: flex;
  margin-bottom: 1.4rem;
}

.YourOrderImgDiv {
  width: 100px;
  height: 100px;
  margin-right: 1rem;
}

.YourOrderImgDiv img {
  width: auto;
  height: 100%;
  object-fit: contain;
}

.YourOrderContentDiv h2 {
  font-size: 1.2rem;
  font-weight: 500;
  margin-bottom: 0.2rem;
  color: var(--linkColor);
}

.YourOrderContentDiv p {
  font-size: 0.6rem;
}

.TopNavbarDiv {
  z-index: 999;
  width: 100%;
}

.fixNavbarDiv {
  position: fixed;
}

